import React from 'react'

const PowerSwitch = ({ on, onChange }) => {
    
    const id1 = (Math.trunc(Math.random() * 10000000)).toString(36)
    const id2 = (Math.trunc(Math.random() * 10000000)).toString(36)

    const click = () => {
        navigator.vibrate(15)
        onChange(0, (on === 0) ? 1 : 0)
    }

    return(
        <svg onClick={click} shapeRendering="geometricPrecision" xmlns="http://www.w3.org/2000/svg" preserveAspectRatio="xMidYMid meet" viewBox="0 0 300 300" width="125" height="125">
            <defs>
                <path d="M97 48L93 50 89 52 86 55 82 57 79 60 75 63 72 66 69 69 66 72 63 75 60 79 57 82 55 86 52 89 50 93 48 97 46 101 44 105 42 109 41 114 40 118 38 122 37 127 37 131 36 136 35 141 35 145 35 150 35 155 35 159 36 164 37 169 37 173 38 178 40 182 41 186 42 191 44 195 46 199 48 203 50 207 52 211 55 214 57 218 60 221 63 225 66 228 69 231 72 234 75 237 79 240 82 243 86 245 89 248 93 250 97 252 101 254 105 256 109 258 114 259 118 260 122 262 127 263 131 263 136 264 141 265 145 265 150 265 155 265 159 265 164 264 169 263 173 263 178 262 182 260 186 259 191 258 195 256 199 254 203 252 207 250 211 248 214 245 218 243 221 240 225 237 228 234 231 231 234 228 237 225 240 221 243 218 245 214 248 211 250 207 252 203 254 199 256 195 258 191 259 186 260 182 262 178 263 173 263 169 264 164 265 159 265 155 265 150 265 145 265 141 264 136 263 131 263 127 262 122 260 118 259 114 258 109 256 105 254 101 252 97 250 93 248 89 245 86 243 82 240 79 237 75 234 72 231 69 228 66 225 63 221 60 218 57 214 55 211 52 207 50 203 48 199 46 218 27 219 28 224 31 228 34 233 37 237 40 241 44 245 47 249 51 253 55 256 59 260 63 263 67 266 72 269 76 272 81 274 86 277 91 279 96 281 101 283 106 284 111 286 116 287 122 288 127 289 133 290 139 290 144 290 150 290 156 290 161 289 167 288 173 287 178 286 184 284 189 283 194 281 199 279 204 277 209 274 214 272 219 269 224 266 228 263 233 260 237 256 241 253 245 249 249 245 253 241 256 237 260 233 263 228 266 224 269 219 272 214 274 209 277 204 279 199 281 194 283 189 284 184 286 178 287 173 288 167 289 161 290 156 290 150 290 144 290 139 290 133 289 127 288 122 287 116 286 111 284 106 283 101 281 96 279 91 277 86 274 81 272 76 269 72 266 67 263 63 260 59 256 55 253 51 249 47 245 44 241 40 237 37 233 34 228 31 224 28 219 26 214 23 209 21 204 19 199 17 194 16 189 14 184 13 178 12 173 11 167 10 161 10 156 10 150 10 144 10 139 11 133 12 127 13 122 14 116 16 111 17 106 19 101 21 96 23 91 26 86 28 81 31 76 34 72 37 67 40 63 44 59 47 55 51 51 55 47 59 44 63 40 67 37 72 34 76 31 81 28 83 27 101 46 97 48Z" id={id1}/>
                <path d="M138 10L163 10 163 173 138 173 138 10Z" id={id2}/>
            </defs>
            <use href={'#' + id1} className={ 'PowerSwitch-svg' + (on ? ' on' : '') } />
            <use href={'#' + id2} className={ 'PowerSwitch-svg' + (on ? ' on' : '') } />   
        </svg>
    )
}

export default PowerSwitch


